import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class GeoJsonConfService {

  constructor() { }

  getColor(d: number) {
    return d > 8 ? '#800026' :
      d > 7 ? '#BD0026' :
        d > 6 ? '#E31A1C' :
          d > 5 ? '#FC4E2A' :
            d > 4 ? '#FD8D3C' :
              d > 3 ? '#FEB24C' :
                d > 2 ? '#FED976' :
                  '#FFEDA0';
  }

}
