import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import * as L from 'leaflet';

import { GeoJsonConfService } from './../../services/geoJsonConf.service';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.scss']
})
export class MapComponent implements OnInit, AfterViewInit {

  private map: L.Map;
  private geojson: L.GeoJSON
  private centroid: L.LatLngExpression = [24.657616686228846, 46.79783142089844]; //

  @Input() geoJsonFeatures;
  @Output() selectAnArea = new EventEmitter();


  constructor(private geoJsonConf: GeoJsonConfService) { }





  private getStyle(feature) {

    let getColor = (d: number): string => {
      return d > 8 ? '#800026' :
        d > 7 ? '#BD0026' :
          d > 6 ? '#E31A1C' :
            d > 5 ? '#FC4E2A' :
              d > 4 ? '#FD8D3C' :
                d > 3 ? '#FEB24C' :
                  d > 2 ? '#FED976' :
                    '#FFEDA0';
    }

    let color: string = getColor(feature.properties.population / feature.properties.numberOfHouses);
    return {
      fillColor: color,
      weight: 3,
      opacity: 1,
      color: 'white',
      dashArray: '3',
      fillOpacity: 0.7
    };
  }

  private selected: any;

  private toolTipOptions = { permanent: true, opacity: 0.7 };

  toolTipTogle() {
    this.geojson.getLayers().forEach((layer) => {
      layer.toggleTooltip();
    })
  }


  private initMap(): void {
    this.map = L.map('map', {
      center: this.centroid,
      zoom: 16
    });

    const tiles = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      maxZoom: 18,
      minZoom: 10,
      attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>'
    });

    // create 5 random jitteries and add them to map
    // const jittery = Array(5).fill(this.centroid).map(
    //   x => [x[0] + (Math.random() - .5) / 10, x[1] + (Math.random() - .5) / 10]
    // ).map(
    //   x => L.marker(x as L.LatLngExpression)
    // ).forEach(
    //   x => x.addTo(this.map)
    // );

    tiles.addTo(this.map);

    let onEachFeature = (feature, layer) => {

      layer.bindTooltip(feature.properties.areaName, this.toolTipOptions);

      let highlightFeature = (e) => {
        var layer = e.target;
        if (this.selected !== layer) {
          layer.setStyle({
            weight: 5,
            color: '#666',
            dashArray: '',
            fillOpacity: 0.9
          });
        }
      }

      let resetHighlight = (e) => {
        if (this.selected !== e.target) {
          this.geojson.resetStyle(e.target);
        }
        // e.target.toggleTooltip();
        // console.log();
      }

      let clickOnFeature = (e) => {
        this.map.fitBounds(e.target.getBounds());
        var layer = e.target;
        this.geojson.resetStyle(this.selected);
        this.selected = e.target;
        layer.setStyle({
          weight: 5,
          color: '#2f2f2f',
          dashArray: '',
          fillOpacity: 1
        });
        sendToParent(e)
      }

      let sendToParent = (e) => {

        this.selectAnArea.emit(e.target.feature.properties.id)
        // console.log(e.target.feature);
      }

      layer.on({
        mouseover: highlightFeature,
        mouseout: resetHighlight,
        click: clickOnFeature,
      });
    }

    this.geojson = L.geoJSON(this.geoJsonFeatures, { style: this.getStyle, onEachFeature: onEachFeature }).addTo(this.map);

  }

  resetStyle() {
    this.geojson.resetStyle(this.selected);
    this.selected = {};
  }

  ngOnInit(): void {
    // console.log(geo1, { style: this.style });
  }

  ngAfterViewInit(): void {
    this.initMap();

  }

}
